.navBar {
    background-color: white;
    color: #486499;
    display: flex;
    justify-content: space-between;
    gap: 16px;
    position: sticky;
}


.navLeft {
    margin-left: 36px;
    display: flex;
    gap: 26px;
    color:#486499; 
}

.navRight {
    margin-right: 36px;
    display: flex;
    gap: 26px;
    color:#486499; 
}

.navRight:visited {
    margin-right: 36px;
    display: flex;
    gap: 26px;
    text-decoration: none; 
    color:#486499; 
}

.center {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color:#486499; 

    
}

